
.seller{color: #000;position: absolute;top: 174px;bottom: 0;left: 0;width: 100%;overflow: hidden;
}
.border-1px{border-bottom: 1px solid rgba(7,17,27,0.1);
}
.seller .overview{padding: 18px;
}
.seller .overview .title{font-size: 14px;margin-bottom: 8px;line-height: 14px;color: #07111b;
}
.seller .overview .desc{padding-bottom: 18px;font-size: 0;position: relative;
}
.seller .overview .desc .star{display: inline-block;vertical-align: top;margin-right: 8px;
}
.seller .overview .desc .text{display: inline-block;vertical-align: top;margin-right: 12px;line-height: 18px;font-size: 10px;color: #4d555d;
}
.seller .overview .remark{display: -webkit-box;display: -ms-flexbox;display: flex;padding-top: 18px;
}
.seller .overview .remark .block{-webkit-box-flex: 1;-ms-flex: 1;flex: 1;text-align: center;border-right: 1px solid rgba(7,17,27,0.1);
}
.seller .overview .remark .block:nth-child(3){border: 0;
}
.seller .overview .remark .block h2{font-size: 10px;color: #939995;
}
.seller .overview .remark .block .content{font-size: 10px;color: #07111b;line-height: 24px;
}
.seller .overview .remark .block .content .stress{font-size: 24px;
}
.favorite{position: absolute;right: 18px;top:38px;font-size: 14px;
}
.seller .bulletin .supports .icon{display: inline-block;width: 16px;height: 16px;vertical-align: top;margin-right: 6px;background-size: 16px 16px;background-repeat: no-repeat;
}
.decrease{background: url("decrease_1@2x.png");
}
.discount{background: url("discount_1@2x.png");
}
.special{background: url("special_1@2x.png");
}
.invoice{background: url("invoice_1@2x.png");
}
.guarantee{background: url("guarantee_1@2x.png");
}
.bulletin{padding: 18px;
}
.bulletin .title{font-size: 14px;margin-bottom: 8px;line-height: 14px;color: #07111b;
}
.bulletin .content-wrapper{padding: 0 12px 16px 1px;position: relative;
}
.bulletin .content-wrapper .content{line-height: 24px;
}
.supports .support-item{padding: 16px 12px;position: relative;font-size: 0;
}
.supports .support-item .icon{display: inline-block;width: 16px;height: 16px;vertical-align: top;margin-right: 6px;background-size: 16px 16px;background-repeat: no-repeat;
}
.supports .support-item .text{display: inline-block;font-size: 12px;line-height: 16px;color: #07111b;
}
.pics{padding: 18px;
}
.pics .title{margin-bottom: 12px;line-height: 14px;font-size: 14px;color: #07111b;
}
.pics .pic-wrapper{width: 100%;overflow: hidden;white-space: nowrap;
}
.pics .pic-wrapper .pic-list{font-size: 0;
}
.pics .pic-wrapper .pic-list .pic-item{display: inline-block;margin-right: 6px;width: 120px;height: 90px;
}
.info{padding: 18px 18px 18px 18px;color: #07111b;
}
.info .title{padding-bottom: 12px;line-height: 14px;font-size: 14px;position: relative;
}
.info .info-item{padding: 16px 12px;line-height: 16px;position: relative;font-size: 12px;
}
.info .info-item:after{display: block;position: absolute;left: 0;bottom: 0;border-top: 1px solid rgba(7,17,27,0.1);width: 100%;content: '';
}



