
.goods{color: #000;margin-top: 1px;display:-webkit-box;display:-ms-flexbox;display:flex;position: absolute;width: 100%;top: 174px;bottom: 46px;overflow: hidden;z-index: 99;
}
.goods .menu-wrapper{-webkit-box-flex: 0;-ms-flex: 0 0 80px;flex: 0 0 80px;width: 80px;background: #f3f5f7;
}
.goods .menu-wrapper ul{transition-duration: 0ms;-webkit-transform: translate(0px, 0px) translateZ(0px);transform: translate(0px, 0px) translateZ(0px);
}
.goods .menu-wrapper .menu-item{display:table;width:56px;height:54px;line-height:14px;padding:0 12px;
}
.goods .menu-wrapper .menu-item .text{font-size:12px;display:table-cell;width:56px;vertical-align: middle;position:relative;font-size:12px;
}
.goods .menu-wrapper .menu-item .text .icon{color:#000;
}
.goods .menu-wrapper .menu-item.current{position:relative;z-index:10;margin-top:-1px;background:#fff;font-weight:700;
}
.goods .foods-wrapper{-webkit-box-flex: 1;-ms-flex: 1;flex: 1;
}
.goods .foods-wrapper .food-list .title{padding-left: 24px;height: 26px;line-height: 26px;border-left: 2px solid #d9dde1;font-size: 12px;color: rgb(147,153,159);background: #f3f5f7;
}
.goods .foods-wrapper .food-list .food-item{display: -webkit-box;display: -ms-flexbox;display: flex;margin: 18px;padding-bottom: 18px;position: relative;border-bottom: 1px solid rgba(7,17,27,0.1);
}
.goods .foods-wrapper .food-list .food-item .icon{-webkit-box-flex: 0;-ms-flex: 0 0 57px;flex: 0 0 57px;margin-right: 10px;
}
.goods .foods-wrapper .food-list .food-item .content{-webkit-box-flex: 1;-ms-flex: 1;flex: 1;
}
.goods .foods-wrapper .food-list .food-item .content .name{margin: 2px 0 8px 0;font-size: 14px;line-height: 14px;height: 14px;color: #07111b;
}
.goods .foods-wrapper .food-list .food-item .content .desc{margin-bottom: 8px;line-height: 12px;font-size: 10px;color: #93999f;
}
.goods .foods-wrapper .food-list .food-item .content .extra{line-height: 12px;font-size: 12px;color: #93999f;
}
.goods .foods-wrapper .food-list .food-item .content .price{line-height: 27px;
}
.goods .foods-wrapper .food-list .food-item .content .price .now{margin-right: 8px;font-size: 14px;color: red;
}
.cartControl-wrapper{position: absolute;right: 0;bottom: 12px;width: 20px;height: 26px;line-height: 26px;text-align: center;
}



