
.tab{display:-webkit-box;display:-ms-flexbox;display:flex;width:100%;height:40px;line-height:40px;position:relative;
}
.tab .tab-item{-webkit-box-flex:1;-ms-flex:1;flex:1;text-align:center;
}
.tab .tab-item a{display:block;font-size:14px;color:rgb(77,85,93);
}
.tab .tab-item .active{color:rgb(240,20,20);
}



