
.ratings{color: #000;position: absolute;top: 174px;bottom: 0;left: 0;width: 100%;overflow: hidden;
}
.ratings .overview{display: -webkit-box;display: -ms-flexbox;display: flex;padding:18px 0 18px 18px;
}
.ratings .overview .overview-left{-webkit-box-flex: 0;-ms-flex: 0 0 137px;flex: 0 0 137px;width: 137px;padding: 6px 0;border-right: 1px solid #ccc;text-align: center;
}
.ratings .overview .overview-left .score{margin-bottom: 12px;line-height: 28px;font-size: 24px;color: #f90;
}
.ratings .overview .overview-left .title{margin-bottom: 8px;line-height: 12px;font-size: 12px;color: #07111b;
}
.ratings .overview .overview-left .rank{margin-bottom: 8px;line-height: 12px;font-size: 12px;color: #93999f;
}
.ratings .overview .overview-right{-webkit-box-flex: 1;-ms-flex: 1;flex: 1;padding: 6px 0 6px 24px;
}
.ratings .overview .overview-right .score-wrapper{line-height: 18px;margin-top: 8px;font-size: 0;
}
.ratings .overview .overview-right .score-wrapper .title{display: inline-block;vertical-align: top;line-height: 18px;font-size: 12px;color: #07111b;
}
.ratings .overview .overview-right .delivery-wrapper{line-height: 18px;margin-top: 8px;font-size: 12px;
}
.ratings .overview .overview-right .delivery-wrapper .delivery{color: #93999f;margin: 0 8px;
}
.ratings .overview .overview-right .score-wrapper .star{display: inline-block;margin: 0 12px;vertical-align: top;
}
.ratings .overview .overview-right .score-wrapper .score{display: inline-block;vertical-align: top;line-height: 18px;font-size: 12px;color: #f90;
}
.rating-wrapper{padding:0 18px;
}
ul li{list-style: none;
}
.rating-wrapper .rating-item{display: -webkit-box;display: -ms-flexbox;display: flex;padding:18px 0;position: relative;
}
.rating-wrapper .rating-item .avatar{-webkit-box-flex: 0;-ms-flex: 0 0 28px;flex: 0 0 28px;width: 28px;margin-right: 12px;
}
.rating-wrapper .rating-item .avatar img{border-radius: 50%;
}
.rating-wrapper .rating-item .content{-webkit-box-flex: 1;-ms-flex: 1;flex: 1;position: relative;
}
.rating-wrapper .rating-item .content .name{margin-bottom: 4px;line-height: 12px;font-weight: 700;font-size: 10px;color: #07111b;
}
.rating-wrapper .rating-item .content .delivery{display: inline-block;vertical-align: top;font-size: 10px;line-height: 12px;color: #93999f;
}
.rating-wrapper .rating-item .content .text{line-height: 18px;color: #07111b;font-size: 12px;margin-bottom: 8px;
}
.rating-wrapper .rating-item .content .recommend{font-size: 12px;line-height: 16px;
}
.rating-wrapper .rating-item .content .recommend .item{padding: 0 6px;border: 1px solid rgba(1,17,27,0.1);border-radius: 1px;color: #93999f;margin: 0 6px;
}
.rating-wrapper .rating-item .content .time{position: absolute;top: 0;right: 0;line-height: 12px;font-size: 10px;color: #93999f;
}
.rating-wrapper .rating-item:after{display: block;position: absolute;left: 0;bottom: 0;border-top: 1px solid rgba(1,17,27,0.1);width: 100%;content: '';
}



