
.shopCart{position: fixed;left: 0;bottom: 0;z-index: 999;width: 100%;height: 48px;
}
.shopCart .content{display: -webkit-box;display: -ms-flexbox;display: flex;background: #141d27;
}
.shopCart .content .content-left{-webkit-box-flex: 1;-ms-flex: 1;flex: 1;
}
.shopCart .content .content-left .logo-wrapper{display: inline-block;position: relative;top: -10px;margin: 0 12px;padding: 6px;width: 56px;height: 56px;box-sizing: border-box;vertical-align: top;border-radius: 50%;background: #141d27;
}
.shopCart .content .content-left .logo-wrapper .num{display: none;position: absolute;top: 0;right: 0;width: 24px;height: 24px;line-height: 27px;text-align: center;border-radius: 16px;font-size: 9px;font-weight: 700;color: #fff;background: rgb(240,20,20);box-shadow: 0 4px 8px 0 rgba(0,0,0,0.4);
}
.shopCart .content .content-left .logo-wrapper .logo{width: 100%;height: 100%;text-align: center;border-radius: 50%;background: #2b343c;
}
.shopCart .content .content-left .logo-wrapper .logo.icon-gouwuche{line-height: 44px;font-size: 24px;color: #80858a;
}
.shopCart .content .content-left .price{display: inline-block;vertical-align: top;margin-top: 12px;line-height: 24px;padding-right: 12px;box-sizing: border-box;border-right: 1px solid rgba(255,255,255,0.1);font-size: 16px;font-weight: 700;color: rgba(255,255,255,0.4);
}
.shopCart .content .content-left .desc{display: inline-block;vertical-align: top;line-height: 24px;margin-left: 12px;margin-top: 12px;color: rgba(255,255,255,0.4);font-size: 10px;
}
.shopCart .content .content-right{-webkit-box-flex: 0;-ms-flex: 0 0 105px;flex: 0 0 105px;width: 105px;
}
.shopCart .content .content-right .pay{height: 48px;line-height: 48px;text-align: center;font-size: 12px;color: rgba(255,255,255,0.4);font-weight: 700;background: #2b333b;
}
.shopCart .ball-container .ball{position: fixed;left: 32px;bottom: 22px;z-index: 200;
}
.shopCart .ball-container .ball .inner{width: 16px;height: 16px;border-radius: 50%;background: rgb(0,160,220);transition: all 0.4s linear;
}
.shopCart .shopcart-list{position: absolute;top: 0;left: 0;z-index: -1;width: 100%;
}
.shopCart .shopcart-list .list-header{height: 40px;line-height: 40px;padding: 0 18px;background: #f3f5f7;border-bottom: 1px solid rgba(7,17,27,0.1);
}
.shopCart .shopcart-list .list-header .title{float: left;font-size: 14px;color: rgb(7,17,27);
}
.shopCart .shopcart-list .list-header .empty{float: right;font-size: 12px;color: rgb(0,160,220);
}
.shopCart .shopcart-list .list-content{padding: 0 18px;max-height: 217px;overflow: hidden;background: #fff;
}
.shopCart .shopcart-list .list-content .shopcart-food{position: relative;padding: 12px 0;box-sizing: border-box;
}
.shopCart .shopcart-list .list-content .shopcart-food .name{line-height: 24px;font-size: 14px;color: rgb(7,17,27);
}
.shopCart .shopcart-list .list-content .shopcart-food .price{position: absolute;right: 90px;bottom: 12px;line-height: 24px;font-size: 14px;font-weight: 700;color: rgb(240,20,20);
}
.shopCart .shopcart-list .list-content .shopcart-food .cartControl-wrapper{position: absolute;right: 0;bottom: 6px;
}
.list-mask{position: fixed;top: 0;left: 0;width: 100%;height: 100%;z-index: 40;background: rgba(7,17,27,0.6);display: none;
}
.list-mask.fade-enter-active,.fade-leave-active{opacity: 1;transition: all 0.5s;background: rgba(7,17,27,0.6);
}
.list-mask.fade-enter-active,.fade-leave-active.fade-enter,.fade-leave-active{opacity: 0;background: rgba(7,17,27,0);
}



