
.ratingselect .rating-type{padding:18px 0;margin:0 18px;font-size: 0;
}
.ratingselect .rating-type .block{display: inline-block;padding: 8px 12px;margin-right: 8px;border-radius: 1px;font-size: 12px;
}
.ratingselect .rating-type .block.positive{background: rgba(0,160,220,0.2);
}
.ratingselect .rating-type .block.positive:active{color: #fff;background: rgb(0,160,220);
}
.ratingselect .rating-type .block.negative{background: rgba(77,85,93,0.2);
}
.ratingselect .rating-type .block.negative:active{color: #fff;background: rgb(77,85,93);
}
.ratingselect .rating-type .block .count{font-size: 8px;margin-left: 2px;line-height: 16px;
}
.ratingselect .swith{padding:12px 18px;line-height: 24px;font-size: 12px;color: rgb(147,153,159);border:1px solid rgba(1,17,27,0.1);font-size: 0;
}
.ratingselect .swith.on .iconfont{color: #00c850;display: inline-block;vertical-align: top;font-size: 24px;margin-right: 4px;
}
.ratingselect .swith .text{font-size: 12px;
}



